import deleteHint from '@/pages/hint/deletehint.vue' 
export default {
	components:{
		deleteHint
	   },
	data() {
		return {
			baseUrl:this.$common.baseUrl,
			// 是否显示模态框
			isShow:false,
			// 模态框显示的内容
			title:'是否删除数据',
			
			// 接收请求回来的当天数据
			l_datalist:[],
			r_datalist:[],
			// loadMoreText:'加载中...',
			// 是否显示上拉请求数据提示
			// showLoadMore: false,
			// page: 1,
			// limit: 10,
			//lisize: 0, //本次加载数量
			
			langch:{
				print:'打印',
				shanchu:'删除',
				shijian:'时间',
				zuijia:'最佳数据',
				youxiao:'有效数据',
				laji:'垃圾数据',
				quantian:'全天数据',
				common:'一般数据'
			},
			langen:{
				print:'print',
				shanchu:'delete',
				shijian:'time',
				zuijia:'optimum',
				youxiao:'Effective',
				laji:'garbage',
				quantian:'All day',
				common:'common'
			},
			langtype:'',
			year: new Date().getFullYear(),
			// time: new Date().getHours(),
			week: ['日', '一', '二', '三', '四', '五', '六'][new Date().getDay()],
			month: new Date().getMonth() + 1,
			day: new Date().getDate(),
			avatar:uni.getStorageSync('userinfo').avatar,
			username:uni.getStorageSync('userinfo').username,
			}
	},
	onShow() {
		this.langtype = uni.getStorageSync('lang')?uni.getStorageSync('lang'):'CH'
		this.getTime()
		this._searchtodayData()
	},

	methods: {
		// scrolltolower(){
		// 	this.showLoadMore = true
		// 	setTimeout(() => {
		// 		//获取数据
		// 		if (this.lisize == 10){//
		// 			this.page++;
		// 			this._searchtodayData()
		// 		}else{
		// 			this.loadMoreText = "暂无更多";
		// 			this.showLoadMore = false;
		// 		}
				
		// 	}, 1000)
		// },
		// scrolltoupper(){
		// 	this.page = 1;
		// 	this._searchtodayData()
		// 	setTimeout(function() {
		// 		uni.stopPullDownRefresh(); 
		// 	}, 1000);
		// },

		back(){
			 uni.navigateBack({
				delta:1
			 })
		},
		getTime: function() {
			setInterval(() => {
				var aData = new Date();
				this.year = aData.getFullYear();
				this.month = aData.getMonth() + 1;
				let weeks = ['日', '一', '二', '三', '四', '五', '六']
				this.week = weeks[aData.getDay()]
				this.day = aData.getDate();
				this.min = aData.getMinutes();
			}, 1000)
		},
		// 查询当天数据
		_searchtodayData(){
			let obj = {
			}
			this.$common.requests('/api/index/getOnlineData',obj).then(res=>{
				this.showLoadMore = false;
				if(res.data.code == 1){
					this.l_datalist = res.data.data.l_data
					console.log(this.l_datalist,666)
					this.r_datalist = res.data.data.r_data
				}
				if(res.data.code==0){
					uni.showToast({
						title:res.data.msg,
						icon:'none'
					})
				}
			})
			
		},
		// 监听L数据选中框选择
		checkboxChangeL(index){
			// 把未选中变成选中状态,选中变成未选中状态
			this.l_datalist[index].checked = !this.l_datalist[index].checked
		},
		// 监听R数据选中框选择
		checkboxChangeR(index){
			// 把未选中变成选中状态,选中变成未选中状态
			this.r_datalist[index].checked = !this.r_datalist[index].checked
		},
		// 打印
		print(){
		},
		// 删除模态框
		deletedata(){
			this.isShow = true
		},
		// 单击删除
		deleteclick(){
			// 弹出模态框
			this.isShow = !this.isShow
		},
		// 弹出模态框关闭模态框
		closemod(){
			this.isShow = false
		},
		// 弹出模态框点击取消
		cancleclick(){
			this.isShow = !this.isShow
		},
		// 弹出模态框点击确定就删除选中数据
		tureclick(){
			this.isShow = !this.isShow
			var chooseid = ''
			// 判断是那个数据列表要删除
			// 后台要求删除多个选中id之间用,隔开
				this.l_datalist.forEach(item=>{
					if(item.checked == true){
						chooseid  += (item.id + ',') 
					}
				})
				this.r_datalist.forEach(item=>{
					if(item.checked == true){
						chooseid  += (item.id + ',') 
					}
				})

			chooseid = chooseid.substring(0,chooseid.length-1)
			var obj ={
				ids:chooseid
			}
			this.$common.requests('/api/record/del_record',obj).then(res => {
				uni.showToast({
				    title: res.data.msg,
				    duration: 1000
				});
				// 删除数据数据成功，立刻显示最新数据数据
				// 请求数据分析方法
				this._searchtodayData()
			})	
		}
		
	}
			
			 
